@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Koulen&family=Lato&family=Nunito&family=Playfair+Display:ital@1&family=Prata&family=Raleway:ital,wght@1,100&family=Roboto&family=Roboto+Condensed&family=Teko&display=swap");

.btn {
	padding: 0.7rem 1.5rem;
	font-size: 1.2rem;

	border: 1px solid rgba(255, 255, 255, 0.8);
	background: rgba(255, 255, 255, 0.1);
	color: #f8f8f8;
	border-top-right-radius: 10px;
	border-bottom-right-radius: 10px;
	transition: 100ms;
}
.btn:active {
	background-color: #fff;
	color: black;
	transition: 100ms;
}

body {
	font-family: "Outfit", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
		"Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

p {
	font-size: 1.6rem;
}

h1 {
	font-size: 6rem;
}

.app {
	width: 100%;
	height: 120vh;
	position: relative;
	background-color: rgba(0, 0, 0, 0.4);
	color: #fff;
}

.app:before {
	content: "";
	background: url("./component/images/rains.jpg") no-repeat center center/cover;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	z-index: -1;
}

.app .search {
	text-align: center;
	padding: 1rem;
	display: flex;
	justify-content: center;
}

.app input {
	padding: 0.7rem 1.5rem;
	font-size: 1.2rem;
	border-top-left-radius: 10px;
	border-bottom-left-radius: 10px;
	border: 1px solid rgba(255, 255, 255, 0.8);
	background: rgba(255, 255, 255, 0.1);
	color: #f8f8f8;
}

::placeholder {
	color: #f8f8f8;
}

.container {
	max-width: 700px;
	height: 550px;
	margin: auto;
	padding: 0 1rem;
	position: relative;
	top: 10%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.app .top {
	width: 100%;
	margin: 1rem auto;
}
@media only screen and (min-width: 500px) {
	.app .description {
		position: relative;
		right: -90%;
		transform-origin: 0 0;
		transform: rotate(269deg);
	}
	.btn:hover {
		background-color: #dedada;
		color: black;
		transition: 100ms;
	}
	.btn:active {
		background-color: #fff;
		color: black;
		transition: 100ms;
	}
}
.app .bottom {
	display: flex;
	justify-content: space-evenly;
	text-align: center;
	width: 100%;
	margin: 1rem auto;
	padding: 1rem;
	border-radius: 12px;
	background-color: rgba(255, 255, 255, 0.2);
}

.bold {
	font-weight: 700;
}

@media only screen and (max-width: 400px) {
	.app {
		width: 100%;
		height: 100vh;
		position: relative;
		background-color: rgba(0, 0, 0, 0.4);
		color: #fff;
	}

	p {
		font-size: 1.2rem;
	}
	h1 {
		font-size: 4.5rem;
	}
	.app .description {
		color: white;
		opacity: 0.7;
		font-weight: 700;
	}
	.btn {
		padding: 0.7rem 1.5rem;
		font-size: 1.2rem;

		border: 1px solid rgba(255, 255, 255, 0.8);
		background: rgba(255, 255, 255, 0.1);
		color: #f8f8f8;
		border-top-right-radius: 10px;
		border-bottom-right-radius: 10px;
		transition: 100ms;
	}
	.btn:active {
		background-color: #fff;
		color: black;
		transition: 100ms;
	}
	.app input {
		padding: 0.7rem 1.5rem;
		font-size: 1.2rem;
		border-top-left-radius: 10px;
		border-bottom-left-radius: 10px;
		border: 1px solid rgba(255, 255, 255, 0.8);
		background: rgba(255, 255, 255, 0.1);
		color: #f8f8f8;
		width: 250px;
	}
}
